import React, { Component } from 'react';
import './index.scss';
import '../../../style/style.scss';
import { Card, Button, Icon } from 'zzcDesign';

let isClose = false;

export default class App extends Component {

    constructor( props ) {
        super( props );
    }

    render() {

        return (
            <div className="zzc-demo">
                <div className="zzc-demo-header">
                    <h1 className="zzc-demo-title">Card </h1>
                    <h2>3HeaderBodyFotter</h2>
                </div>
                <div className="zzc-demo-body full">
                    <Card>
                        <Card.Header title="" extra="this is extra" />
                        <Card.Body>
                            <div className="card-box1">
                                <p></p>
                            </div>
                        </Card.Body>
                        <Card.Body>
                            <div className="card-box1">
                                <p></p>
                            </div>
                        </Card.Body>
                        <Card.Footer>
                            <Button noBorder full></Button>
                        </Card.Footer>
                    </Card>
                </div>
                <div className="zzc-demo-body full">
                    <Card full>
                        <Card.Header title="" extra="this is extra" />
                        <Card.Body borderDirection='left'>
                            <div className="card-box2">
                                <p></p>
                                <Icon size="sm" type="success"/>
                            </div>
                        </Card.Body>
                        <Card.Body>
                            <div className="card-box2">
                                <p></p>
                                <Icon size="sm" type="success"/>
                            </div>
                        </Card.Body>
                    </Card>
                </div>
            </div>
        );
    }
}


